<h1 id="android-bluetooth-classic" tabindex="-1"><a class="header-anchor" href="#android-bluetooth-classic" aria-hidden="true">#</a> Android Bluetooth Classic</h1>
<p>You want your application to communicate with an application running on another device? Then Bluetooth could be the technology you need. Let's take a look at how <em>Bluetooth 3</em> (AKA <em>Bluetooth Classic</em> from Bluetooth 4 and on) works.</p>
